























































































































import { Component, Vue, Prop, Ref } from 'vue-property-decorator';
import paramsCache from '@mixins/paramCache';
import circulationDetails from './components/circulationDetails.vue';
import deleOrTransDialog from './components/deleOrTransDialog.vue';
import { obj2FormData } from '@utils/util';
import {
  getNodeListById,
  queryDealtTaskList,
  dealClaim,
  queryCirculateList,
  dealReview,
  reFetch
} from '@api/processAssignment';

const paramsCacheMixin = paramsCache(['queryParams']);

@Component({
  components: { circulationDetails, deleOrTransDialog },
  mixins: [paramsCacheMixin]
})
export default class NeedDealtTask extends Vue {
  @Prop({ default: [] }) readonly processTagsList: any;
  @Prop({ default: [] }) readonly processNameList: any;
  @Ref() deleOrTrans: any;
  private queryParams: any = {
    emergencyState: 0,
    processName: [],
    taskName: [],
    processTags: [],
    limit: 20,
    page: 1
  };
  private taskNameList: Array<any> = [];

  private tableDataList: Array<any> = [];
  private tableTotal = 0;
  private actionList: Array<any> = [
    { name: '认领', action: 'claim' },
    { name: '委托', action: 'delegation' },
    { name: '退回', action: 'reject' },
    { name: '移交', action: 'transfer' },
    { name: '传阅', action: 'circulate' },
    { name: '结束流程', action: 'end' },
    { name: '重取', action: 'tryAgain' }
  ];
  private defaultActions: Array<any> = [
    { name: '办理', action: 'handle' },
    { name: '流转历史', action: 'flowHistory' }
  ];
  private circulateList: Array<any> = [
    { name: '查看流转历史', action: 'flowHistory' },
    { name: '审阅', action: 'doReview' }
  ];
  private columnsList: Array<any> = [
    {
      prop: '',
      label: '序号',
      type: 'index',
      width: 60,
      align: 'center'
    },
    {
      prop: 'processName',
      label: '流程名称',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'launchingMode',
      label: '发起方式',
      minWidth: 120,
      'header-align': 'left'
    },
    {
      prop: 'productCode',
      label: '产品主代码',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'productContractAbbreviation',
      label: '产品合同简称',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'launchingTime',
      label: '发起时间',
      'header-align': 'left',
      width: 170
    },
    // {
    //   prop: 'processTags',
    //   label: '流程标签',
    //   'header-align': 'left',
    //   minWidth: 140,
    //   'show-overflow-tooltip': true
    // },
    {
      prop: 'taskName',
      label: '任务节点名称',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'taskArrivalTime',
      label: '到达任务节点时间',
      'header-align': 'left',
      width: 170
    },
    {
      prop: 'processStatus',
      label: '流程状态',
      minWidth: 140,
      align: 'center',
      render: (h: any, { record }: any) => {
        if (record.processStatus) {
          return h(
            'p',
            {
              style: {
                margin: '0'
              }
            },
            record.processStatus.map((item: any) => {
              return h(
                'el-tag',
                {
                  class: {
                    tag: true,
                    'tag-normal': item === '正常',
                    'tag-will': item === '将要超时',
                    'tag-timeout': item === '超时' || item === '催办'
                  }
                },
                item
              );
            })
          );
        }
      }
    },
    {
      prop: 'taskStatus',
      label: '任务节点状态',
      minWidth: 140,
      align: 'center',
      render: (h: any, { record }: any) => {
        if (record.taskStatus) {
          return h(
            'p',
            {
              style: {
                margin: '0'
              }
            },
            record.taskStatus.map((item: any) => {
              return h(
                'el-tag',
                {
                  class: {
                    tag: true,
                    'tag-normal': item === '正常',
                    'tag-will': item === '将要超时',
                    'tag-timeout': item === '超时' || item === '催办'
                  }
                },
                item
              );
            })
          );
        }
      }
    },
    {
      label: '操作',
      minWidth: 200,
      align: 'center',
      fixed: 'right',
      render: (h: any, { record }: any) => {
        if (this.getCurrentState() === 3) {
          record.actionList = this.circulateList;
        } else {
          record.actionList = record.operationAuthority
            ? [
                ...this.defaultActions,
                ...this.actionList.filter(
                  (item) => record.operationAuthority.indexOf(item.name) > -1
                )
              ]
            : this.defaultActions;
        }

        const action: any =
          record.actionList.length > 2 ? record.actionList.slice(2) : [];
        return h('div', {}, [
          h(
            'el-button',
            {
              props: {
                type: 'text'
              },
              style: {
                display: record.actionList ? '' : 'none'
              },
              on: {
                click: () => {
                  for (const k in record) {
                    this.$set(this.taskMsg, k, record[k]);
                  }
                  record.actionList[0]
                    ? this[record.actionList[0].action]()
                    : '';
                }
              }
            },
            record.actionList[0] ? record.actionList[0].name : ''
          ),
          h(
            'span',
            {
              style: {
                padding: '0 6px',
                display: record.actionList[1] ? '' : 'none'
              }
            },
            '|'
          ),
          h(
            'el-button',
            {
              props: {
                type: 'text'
              },
              style: {
                display: record.actionList[1] ? '' : 'none'
              },
              on: {
                click: () => {
                  for (const k in record) {
                    this.$set(this.taskMsg, k, record[k]);
                  }
                  record.actionList[1]
                    ? this[record.actionList[1].action]()
                    : '';
                }
              }
            },
            record.actionList[1] ? record.actionList[1].name : ''
          ),
          h(
            'span',
            {
              style: {
                padding: '0 6px',
                display: action.length > 0 ? '' : 'none'
              }
            },
            '|'
          ),
          h(
            'el-dropdown',
            {
              on: {
                command: this.handleCommand.bind(this, record)
              },
              props: {
                trigger: 'click'
              },
              style: {
                display: action.length > 0 ? '' : 'none'
              }
            },
            [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  }
                },
                [
                  '更多',
                  h('i', {
                    class: ['icon', 'el-icon-caret-bottom', 'themeIcon']
                  })
                ]
              ),
              h(
                'el-dropdown-menu',
                {
                  slot: 'dropdown'
                },
                action.map((item: any) => {
                  return h(
                    'el-dropdown-item',
                    {
                      props: {
                        command: item.action
                      }
                    },
                    item.name
                  );
                })
              )
            ]
          )
        ]);
      }
    }
  ];
  private tableLoading = false;
  private delegationDialog = false;

  private taskMsg: any = {};
  private histroyDialog = false;

  private mounted() {
    this.getDealtTaskList();
  }
  init() {
    this.getDealtTaskList();
  }

  getCurrentState() {
    return this.queryParams.emergencyState;
  }
  // 流程名称切换
  nameChange(e: any) {
    this.taskNameList = [];
    this.queryParams.taskName = [];
    if (e.length === 1) {
      const params = { processDefinitionKeys: e };
      getNodeListById({ processDefinitionKeys: e }).then(({ error, data }) => {
        if (!error) {
          this.taskNameList = data;
        }
      });
    }
  }
  // 标签点击
  selectTag(tag: any) {
    if (tag === '全部') {
      if (this.queryParams.processTags.length === this.processTagsList.length) {
        this.queryParams.processTags = [];
      } else {
        this.queryParams.processTags = JSON.parse(
          JSON.stringify(this.processTagsList)
        );
      }
    } else {
      const tagIndex = this.queryParams.processTags.indexOf(tag);
      if (tagIndex === -1) {
        this.queryParams.processTags.push(tag);
        if (
          this.queryParams.processTags.length ===
          this.processTagsList.length - 1
        ) {
          this.queryParams.processTags.push('全部');
        }
      } else {
        this.queryParams.processTags.splice(tagIndex, 1);
        const allTag = this.queryParams.processTags.indexOf('全部');
        if (allTag !== -1) {
          this.queryParams.processTags.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }
  // 待办列表查询
  getDealtTaskList() {
    // const params = JSON.parse(JSON.stringify(this.queryParams));
    const params = {
      emergencyState: this.queryParams.emergencyState,
      limit: this.queryParams.limit,
      page: this.queryParams.page,
      templateIds: this.queryParams.processName,
      processTags: JSON.parse(JSON.stringify(this.queryParams.processTags)),
      nodeIds: this.queryParams.taskName
    };

    if (params.processTags.indexOf('全部') !== -1) {
      const index = params.processTags.indexOf('全部');
      params.processTags.splice(index, 1);
    }

    this.tableLoading = true;
    let fun = queryDealtTaskList;
    if (this.queryParams.emergencyState === 3) {
      fun = queryCirculateList;
      params.emergencyState = '0';
    }
    fun(params).then((res) => {
      this.tableLoading = false;
      if (res.data) {
        this.tableTotal = res.data.total;
        this.tableDataList = res.data.rows;
      } else {
        this.$message.error('待办表格数据查询失败：' + res.error.message);
      }
    });
  }
  // 改变页码
  handleCurrent(el: any) {
    this.queryParams.page = el;
    this.getDealtTaskList();
  }
  // 改变页大小
  handleSize(el: any) {
    this.queryParams.limit = el;
    this.queryParams.page = 1;
    this.getDealtTaskList();
  }
  // 搜索
  handleSearch() {
    this.queryParams.page = 1;
    this.getDealtTaskList();
  }
  // 重置
  handleReset() {
    this.queryParams.processName = [];
    this.queryParams.taskName = [];
    this.queryParams.processTags = [];
    this.queryParams.page = 1;
  }
  // 点击更多菜单项触发事件
  handleCommand(row: any, command: any) {
    this.taskMsg = row;
    if (command === 'claim' || command === 'tryAgain') {
      this[command]();
    } else {
      setTimeout(() => {
        this.deleOrTrans[command]();
      }, 100);
    }
  }
  // 认领
  claim() {
    this.$confirm('确定要认领该任务吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const params = { taskId: this.taskMsg.taskId };
        dealClaim(obj2FormData(params)).then((res: any) => {
          if (res.error) {
            this.$message.error('认领失败：' + res.error.message);
          } else {
            this.$message.success('认领成功');
            this.getDealtTaskList();
          }
        });
      })
      .catch(() => {});
  }
  // 查看流转历史
  flowHistory() {
    // 弹出任务办理状态流程流程图
    this.histroyDialog = true;
  }
  // 关闭流转历史弹出
  cancelHistroyDialog() {
    this.histroyDialog = false;
  }
  // 办理
  handle() {
    this.goDeal('deal');
  }
  doReview() {
    this.goDeal('review');
  }
  goDeal(mode) {
    // 跳转任务办理页面
    this.cacheParamData();
    this.$router.push({
      path: '/processCenter/taskDeal',
      query: {
        id: this.taskMsg.id,
        taskId: this.taskMsg.taskId,
        processInstanceId: this.taskMsg.processInstanceId,
        processDefinitionId: this.taskMsg.processDefinitionId,
        taskDefinitionKey: this.taskMsg.taskDefinitionKey,
        mode: mode
      }
    });
  }
  // 审阅
  review() {
    dealReview({ id: this.taskMsg.id }).then((res: any) => {
      if (res.error) {
        this.$message.error('审阅失败：' + res.error.message);
      } else {
        this.$message.success('审阅成功');
        this.getDealtTaskList();
      }
    });
  }
  tryAgain() {
    this.$confirm('确定要重新取数吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        reFetch(this.currentTaskId).then((res) => {
          if (res.error) {
            this.$message.error('取数失败：' + res.error.message);
          } else {
            this.$message.success('取数成功');
          }
        });
      })
      .catch(() => {});
  }
}
