






























import { Component, Vue } from 'vue-property-decorator';
import needDealtTask from './needDealtTask.vue';
import needActivatedTask from './needActivatedTask.vue';
import trackingTask from './trackingTask.vue';
import {
  queryProcessTagsList,
  queryNameList
} from '../../api/processAssignment';

@Component({
  components: { needDealtTask, needActivatedTask, trackingTask }
})
export default class ProcessAssignment extends Vue {
  private tabList: any = [
    { id: '0', name: '待办任务', component: 'needDealtTask' },
    { id: '1', name: '待发起任务', component: 'needActivatedTask' },
    { id: '2', name: '任务跟踪', component: 'trackingTask' }
  ];
  private tabActive = 'needDealtTask';
  private processTagsList: Array<any> = [];
  private processNameList: Array<any> = [];

  tabChoose(currentCom: any) {
    this.tabActive = currentCom;
    const current: any = this.$refs[currentCom];
    const init = current['init'];
    init();
    this.getProcessName();
  }
  // 标签列表查询
  getProcessTags() {
    queryProcessTagsList().then((res) => {
      if (res.data) {
        res.data.unshift('全部');
        this.processTagsList = res.data;
      } else {
        this.$message.error('标签列表查询失败：' + res.error.message);
      }
    });
  }
  // 模板名称查询
  getProcessName() {
    if (
      this.tabActive !== 'needActivatedTask' &&
      this.processNameList.length === 0
    ) {
      queryNameList().then((res) => {
        if (res.data) {
          this.processNameList = res.data;
        } else {
          this.$message.error('标签列表查询失败：' + res.error.message);
        }
      });
    }
  }

  private mounted() {
    this.getProcessTags();
    this.getProcessName();
  }
}
