







































import jquery from 'jquery';
import BpmnViewer from 'bpmn-js/lib/Viewer';
import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import customModule from './customViewRender';
import { arrayFind } from '@utils/util';
import { tempPublishedDetail, tempDetail } from '@api/processConfig';
import { stationQuery, userQueryByGroup } from '@api/processConfig';

@Component({
  components: {}
})
export default class FlowChartPreview extends Vue {
  @Prop() tempId: string;
  @Prop() visible: boolean;
  @Prop({ default: false }) viewPublish: boolean;

  private containerEl: any = null;
  private bpmnModeler: any = null;
  private stations: Array<any> = [];
  private users: Array<any> = [];
  private transactorGroup = '';
  private defaultTransactor = '';
  private scale = 1;
  @Watch('visible')
  switchTask(newVal) {
    if (newVal) {
      this.previewTemp(this.tempId);
    }
  }
  previewTemp(id) {
    this.transactorGroup = '';
    this.defaultTransactor = '';
    const viewInterface = this.viewPublish ? tempPublishedDetail : tempDetail;
    viewInterface({ id }).then(({ data, error }) => {
      this.containerEl = document.getElementById('container');
      this.bpmnModeler && this.bpmnModeler.destroy();
      this.scale = 1;
      this.bpmnModeler = new BpmnViewer({
        container: this.containerEl,
        additionalModules: [customModule]
      });
      const viewer = this.bpmnModeler;
      if (!error) {
        this.bpmnModeler.importXML(data.chart, (err) => {
          if (err) {
            console.error(err);
          } else {
            const canvas = viewer.get('canvas');
            canvas.zoom('fit-viewport');
            //  注册鼠标悬浮事件
            const eventBus = this.bpmnModeler.get('eventBus');
            const overlays = this.bpmnModeler.get('overlays');
            eventBus.on('element.hover', (e) => {
              if (e.element.type === 'bpmn:UserTask') {
                this.transactorGroup = '';
                this.defaultTransactor = '';
                const extensionElements =
                  e.element['businessObject']['extensionElements'];
                if (extensionElements) {
                  const attrList = extensionElements.values[0].$children;
                  if (attrList) {
                    const station = arrayFind(
                      attrList,
                      'name',
                      'transactorGroup'
                    );
                    if (station) {
                      const tempObj = arrayFind(
                        this.stations,
                        'id',
                        station['$body']
                      );
                      this.transactorGroup = tempObj ? tempObj.name : '';
                      const defaultTransactorObj = arrayFind(
                        attrList,
                        'name',
                        'defaultTransactor'
                      );
                      if (
                        defaultTransactorObj &&
                        defaultTransactorObj['$body']
                      ) {
                        this.getUserList(
                          station['$body'],
                          defaultTransactorObj['$body'],
                          overlays,
                          e
                        );
                      } else {
                        this.showSuspendedLayer(overlays, e);
                      }
                    } else {
                      this.transactorGroup = '';
                    }
                  }
                }
              }
            });
            eventBus.on('element.out', (e) => {
              if (e.element.type === 'bpmn:UserTask') {
                overlays.clear();
              }
            });
          }
        });
      } else {
        this.$message.error(error.message);
      }
    });
  }
  showSuspendedLayer(overlays, e) {
    let $overlayHtml;
    if (this.defaultTransactor) {
      $overlayHtml = jquery(` <div class="tipBox">
      <div style="margin-bottom: 5px">办理岗位：${this.transactorGroup}</div>
        <div>默认处理人： ${this.defaultTransactor}</div>
      </div>`);
    } else {
      $overlayHtml = jquery(` <div class="tipBox">
        <div style="margin-bottom: 5px">办理岗位：${this.transactorGroup}</div>
        </div>`);
    }
    overlays.add(e.element.id, {
      position: { top: e.element.height, left: 0 },
      html: $overlayHtml
    });
  }
  getStationList() {
    stationQuery().then(({ data, error }) => {
      if (!error) {
        if (data && data.length) {
          this.stations = data;
        }
      } else {
        this.$message.error(error.message);
      }
    });
  }
  getUserList(stationaId, usercode, overlays, e) {
    // 查看用户缓存中是否有指定用户，有直接取，没有则查询
    const user = arrayFind(this.users, 'id', usercode);
    if (user) {
      this.defaultTransactor = user.username;
      this.showSuspendedLayer(overlays, e);
    } else {
      userQueryByGroup(stationaId).then(({ data, error }) => {
        if (!error) {
          if (data && data.length) {
            const userObj = arrayFind(data, 'id', usercode);
            if (userObj) {
              this.defaultTransactor = userObj.username;
              this.showSuspendedLayer(overlays, e);
            }
            this.users = [...this.users, ...data];
          }
        } else {
          this.$message.error(error.message);
        }
      });
    }
  }
  handleZoom(flag) {
    if (flag < 0 && this.scale <= 1) {
      return;
    }
    if (flag > 0 && this.scale >= 3) {
      return;
    }
    this.scale += flag;
    this.$nextTick(() => {
      this.bpmnModeler.get('canvas').zoom('fit-viewport');
    });
  }
  cancelHistroyDialog() {
    this.$emit('cancel');
  }
  created() {
    this.getStationList();
  }
}
