
























import { Component, Vue, Prop } from 'vue-property-decorator';
import { obj2FormData } from '@utils/util';
import {
  dealDelegation,
  dealEnd,
  dealTransfer,
  dealReject,
  dealCirculate,
  queryCanRejectList
} from '@api/processAssignment';

import { userDictQuery } from '@api/authority';
import { getStorage } from '@utils/storage';

@Component({})
export default class DeleOrTransDialog extends Vue {
  @Prop() taskId: string;
  @Prop() processDefinitionId: string;
  @Prop() taskDefinitionKey: string;
  private dialogTitle = '';
  private selected = '';
  private selectedName = ''; //选中的username
  private selectedMsg = '';
  private delegationDialog = false;
  private selectedList: Array<any> = [];
  private currentUser = getStorage('username'); // 当前登录的用户
  // 委托
  delegation() {
    this.selectedMsg = '请选择委托人';
    this.delegationDialog = true;
    this.dialogTitle = '任务委托';
    this.getCirculateList();
  }
  // 委托确认
  sureDelegation() {
    const params = {
      taskId: this.taskId,
      userId: this.selected
    };
    dealDelegation(obj2FormData(params)).then((res) => {
      if (res.error) {
        this.$message.error('委托失败：' + res.error.message);
      } else {
        this.$message.success('委托成功');
        this.delegationDialog = false;
        this.selectedList = [];
        this.selected = '';
        this.$emit('done');
      }
    });
  }
  // 退回
  reject() {
    this.selectedMsg = '请选择退回节点';
    this.dialogTitle = '任务退回';
    this.getCanRejectList();
    this.delegationDialog = true;
  }
  // 获取可退回节点列表
  getCanRejectList() {
    // const params = {
    //   processDefinitionId: this.processDefinitionId,
    //   taskDefinitionKey: this.taskDefinitionKey
    // };
    queryCanRejectList(this.taskId).then((res) => {
      if (res.data) {
        this.selectedList = res.data.map((item) => {
          const obj = {
            id: item.nodeId,
            name: item.nodeName,
            value: item.nodeName
          };
          return obj;
        });
      } else {
        this.$message.error('可退回节点查询失败：' + res.error.message);
      }
    });
  }
  // 确认退回
  sureReject() {
    const params = {
      activityId: this.selected,
      taskId: this.taskId
    };
    dealReject(obj2FormData(params)).then((res) => {
      if (res.error) {
        this.$message.error('退回失败：' + res.error.message);
      } else {
        this.$message.success('退回成功');
        this.delegationDialog = false;
        this.selectedList = [];
        this.selected = '';
        this.$emit('done');
      }
    });
  }
  // 移交
  transfer() {
    this.selectedMsg = '请选择移交人';
    this.dialogTitle = '任务移交';
    this.getCirculateList();
    this.delegationDialog = true;
  }
  // 移交确认
  sureTransfer() {
    const params = {
      taskId: this.taskId,
      transferUserId: this.selected
    };
    dealTransfer(obj2FormData(params)).then((res) => {
      if (res.error) {
        this.$message.error('移交失败：' + res.error.message);
      } else {
        this.$message.success('移交成功');
        this.delegationDialog = false;
        this.selectedList = [];
        this.selected = '';
        this.$emit('done');
      }
    });
  }
  // 传阅
  circulate() {
    this.selectedMsg = '请选择传阅人';
    this.dialogTitle = '任务传阅';
    this.delegationDialog = true;
    this.getCirculateList();
  }
  // 传阅列表查询
  getCirculateList() {
    userDictQuery().then((res) => {
      if (res.data) {
        this.selectedList = res.data.map((item) => {
          const obj = {
            id: item.usercode,
            name: item.username,
            value: item.username
          };
          return obj;
        });
        this.selectedList = this.selectedList.filter(
          (item) => item.id != this.currentUser
        );
      } else {
        this.$message.error('列表查询失败：' + res.error.message);
      }
    });
  }
  // 传阅确认
  sureCirculate() {
    const params = {
      taskId: this.taskId,
      processDefinitionId: this.processDefinitionId,
      taskDefinitionKey: this.taskDefinitionKey,
      circulateUser: this.selected
    };
    dealCirculate(params).then((res) => {
      if (res.error) {
        this.$message.error('传阅失败：' + res.error.message);
      } else {
        this.$message.success('传阅成功');
        this.delegationDialog = false;
        this.selectedList = [];
        this.selected = '';
        this.$emit('done');
      }
    });
  }
  // 结束
  end() {
    this.$confirm('确定要结束该任务吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const params = { taskId: this.taskId };
        dealEnd(obj2FormData(params)).then((res) => {
          if (res.error) {
            this.$message.error('结束失败：' + res.error.message);
          } else {
            this.$message.success('结束成功');
            this.$emit('done');
          }
        });
      })
      .catch(() => {});
  }
  // 转发
  forward() {
    this.selectedMsg = '请选择转发人';
    this.dialogTitle = '任务转发';
    this.getCirculateList();
    this.delegationDialog = true;
  }
  // 转发确认
  sureForward() {}
  // 确认按钮
  saveDialog() {
    if (!this.selected) {
      this.$message.error(this.selectedMsg);
      return false;
    }
    if (this.dialogTitle === '任务委托') {
      this.sureDelegation();
    } else if (this.dialogTitle === '任务移交') {
      this.sureTransfer();
    } else if (this.dialogTitle === '任务传阅') {
      this.sureCirculate();
    } else if (this.dialogTitle === '任务退回') {
      this.sureReject();
    } else if (this.dialogTitle === '任务转发') {
      this.sureForward();
    }
  }
  // 取消按钮
  cancelDialog() {
    this.selected = '';
    this.selectedName = '';
    this.selectedMsg = '';
    this.selectedList = [];
    this.delegationDialog = false;
    this.$emit('cancel');
  }

  // el-autocomplete默认操作
  querySearch(queryString, cb) {
    const selectedList = this.selectedList;
    const results = queryString
      ? selectedList.filter(this.createFilter(queryString))
      : selectedList;
    // 调用callback返回建议的列表的数据
    cb(results);
  }
  createFilter(queryString) {
    return (item) => {
      return item.name.indexOf(queryString) > -1;
    };
  }
  transferSelect(person) {
    this.selected = person.id;
    this.selectedName = person.value;
  }
}
