





































































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import circulationDetails from './components/circulationDetails.vue';
import { obj2FormData } from '@utils/util';
import {
  dealTakeOver,
  dealWithdraw,
  dealProcessUrge,
  dealNodeUrge,
  queryTrackingTaskList,
  queryCanSkipList,
  dealSkip,
  getNodeListById,
  dealEnd
} from '../../api/processAssignment';

@Component({
  components: { circulationDetails }
})
export default class TrackingTask extends Vue {
  @Prop({ default: [] }) readonly processTagsList: any;
  @Prop({ default: [] }) readonly processNameList: any;
  private queryParams: any = {
    type: '1',
    processName: [],
    processNode: [],
    processTags: [],
    limit: 20,
    page: 1
  };
  // private processTagsList: Array<any> = []
  private tableDataList: Array<any> = [];
  private tableTotal = 0;
  private actionList: Array<any> = [
    { name: '跳过', action: 'skip' },
    { name: '接管', action: 'takeOver' },
    { name: '取回', action: 'withdraw' },
    { name: '结束', action: 'end' }
  ];
  private defaultActions: Array<any> = [
    { name: '流转历史', action: 'flowHistory' },
    { name: '节点催办', action: 'nodeUrge' },
    { name: '流程催办', action: 'processUrge' }
  ];
  private columnsList: Array<any> = [
    {
      prop: '',
      label: '序号',
      type: 'index',
      width: 60,
      align: 'center'
    },
    {
      prop: 'processName',
      label: '流程名称',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'launchingMode',
      label: '发起方式',
      minWidth: 120,
      'header-align': 'left'
    },
    {
      prop: 'productCode',
      label: '产品主代码',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'productContractAbbreviation',
      label: '产品合同简称',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'launchingTime',
      label: '发起时间',
      'header-align': 'left',
      width: 170
    },
    {
      prop: 'processTags',
      label: '流程标签',
      'header-align': 'left',
      minWidth: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'taskName',
      label: '任务节点名称',
      'header-align': 'left',
      width: 140,
      'show-overflow-tooltip': true
    },
    {
      prop: 'taskArrivalTime',
      label: '到达任务节点时间',
      'header-align': 'left',
      width: 170
    },
    {
      prop: 'processStatus',
      label: '流程状态',
      minWidth: 140,
      align: 'center',
      render: (h: any, { record }: any) => {
        return h(
          'p',
          {
            style: {
              margin: '0'
            }
          },
          record.processStatus.map((item: any) => {
            return h(
              'el-tag',
              {
                class: {
                  tag: true,
                  'tag-normal': item === '正常',
                  'tag-will': item === '将要超时',
                  'tag-timeout': item === '超时' || item === '催办'
                }
              },
              item
            );
          })
        );
      }
    },
    {
      prop: 'taskStatus',
      label: '任务节点状态',
      minWidth: 140,
      align: 'center',
      render: (h: any, { record }: any) => {
        return h(
          'p',
          {
            style: {
              margin: '0'
            }
          },
          record.taskStatus.map((item: any) => {
            return h(
              'el-tag',
              {
                class: {
                  tag: true,
                  'tag-normal': item === '正常',
                  'tag-will': item === '将要超时',
                  'tag-timeout': item === '超时' || item === '催办'
                }
              },
              item
            );
          })
        );
      }
    },
    {
      label: '操作',
      minWidth: 240,
      align: 'center',
      fixed: 'right',
      render: (h: any, { record }: any) => {
        if (record.operationAuthority) {
          record.actionList = [
            ...this.defaultActions,
            ...this.actionList.filter(
              (item) => record.operationAuthority.indexOf(item.name) > -1
            )
          ];
        } else {
          record.actionList = this.defaultActions;
        }
        const action: any =
          record.actionList.length > 2 ? record.actionList.slice(2) : [];
        return h('div', {}, [
          h(
            'el-button',
            {
              props: {
                type: 'text'
              },
              style: {
                display: record.actionList ? '' : 'none'
              },
              on: {
                click: () => {
                  for (const k in record) {
                    this.$set(this.taskMsg, k, record[k]);
                  }
                  record.actionList[0]
                    ? this[record.actionList[0].action]()
                    : '';
                }
              }
            },
            record.actionList[0] ? record.actionList[0].name : ''
          ),
          h(
            'span',
            {
              style: {
                padding: '0 6px',
                display: record.actionList[1] ? '' : 'none'
              }
            },
            '|'
          ),
          h(
            'el-button',
            {
              props: {
                type: 'text'
              },
              style: {
                display: record.actionList[1] ? '' : 'none'
              },
              on: {
                click: () => {
                  for (const k in record) {
                    this.$set(this.taskMsg, k, record[k]);
                  }
                  record.actionList[1]
                    ? this[record.actionList[1].action]()
                    : '';
                }
              }
            },
            record.actionList[1] ? record.actionList[1].name : ''
          ),
          h(
            'span',
            {
              style: {
                padding: '0 6px',
                display: action.length > 0 ? '' : 'none'
              }
            },
            '|'
          ),
          h(
            'el-dropdown',
            {
              on: {
                command: this.handleCommand.bind(this, record)
              },
              props: {
                trigger: 'click'
              },
              style: {
                display: action.length > 0 ? '' : 'none'
              }
            },
            [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  }
                },
                [
                  '更多',
                  h('i', {
                    class: ['icon', 'el-icon-caret-bottom', 'themeIcon']
                  })
                ]
              ),
              h(
                'el-dropdown-menu',
                {
                  slot: 'dropdown'
                },
                action.map((item) => {
                  return h(
                    'el-dropdown-item',
                    {
                      props: {
                        command: item.action
                      }
                    },
                    item.name
                  );
                })
              )
            ]
          )
        ]);
      }
    }
  ];
  private tableLoading = false;
  private taskMsg: any = {};
  private dialogTitle = '';
  private dialogVisible = false;
  private dialogSelect = '';
  private dialogSelectList: Array<any> = [];
  private dialogSelectContent = '';
  private histroyDialog = false;
  private taskNameList: Array<any> = [];

  init() {
    this.getTrackingTaskList();
  }
  // 标签点击
  selectTag(tag: any) {
    if (tag === '全部') {
      if (this.queryParams.processTags.length === this.processTagsList.length) {
        this.queryParams.processTags = [];
      } else {
        this.queryParams.processTags = JSON.parse(
          JSON.stringify(this.processTagsList)
        );
      }
    } else {
      const tagIndex = this.queryParams.processTags.indexOf(tag);
      if (tagIndex === -1) {
        this.queryParams.processTags.push(tag);
        if (
          this.queryParams.processTags.length ===
          this.processTagsList.length - 1
        ) {
          this.queryParams.processTags.push('全部');
        }
      } else {
        this.queryParams.processTags.splice(tagIndex, 1);
        const allTag = this.queryParams.processTags.indexOf('全部');
        if (allTag !== -1) {
          this.queryParams.processTags.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }
  // 流程名称切换
  nameChange(e: any) {
    this.taskNameList = [];
    this.queryParams.processNode = [];
    if (e.length === 1) {
      getNodeListById({ processDefinitionKeys: e }).then(({ error, data }) => {
        if (!error) {
          this.taskNameList = data;
        }
      });
    }
  }
  // 任务跟踪列表查询
  getTrackingTaskList() {
    const params = {
      templateIds: this.queryParams.processName,
      processTags: JSON.parse(JSON.stringify(this.queryParams.processTags)),
      nodeIds: this.queryParams.processNode,
      trackType: this.queryParams.type,
      limit: this.queryParams.limit,
      page: this.queryParams.page
    };
    if (params.processTags.indexOf('全部') !== -1) {
      const index = params.processTags.indexOf('全部');
      params.processTags.splice(index, 1);
    }
    this.tableLoading = true;
    queryTrackingTaskList(params).then((res) => {
      this.tableLoading = false;
      if (res.data) {
        this.tableDataList = res.data.rows;
        this.tableTotal = res.data.total;
      } else {
        this.$message.error('任务跟踪列表查询失败：' + res.error.message);
      }
    });
  }
  // 改变页码
  handleCurrent(el: any) {
    this.queryParams.page = el;
    this.getTrackingTaskList();
  }
  // 改变页大小
  handleSize(el: any) {
    this.queryParams.limit = el;
    this.queryParams.page = 1;
    this.getTrackingTaskList();
  }
  // 搜索
  handleSearch() {
    this.queryParams.page = 1;
    this.getTrackingTaskList();
  }
  // 重置
  handleReset() {
    this.queryParams.processName = [];
    this.queryParams.processNode = [];
    this.queryParams.processTags = [];
    this.queryParams.limit = 20;
    this.queryParams.page = 1;
  }
  // 点击更多菜单项触发事件
  handleCommand(row: any, command: any) {
    // console.log(row, command)
    this.taskMsg = row;
    this[command]();
  }
  // 接管
  takeOver() {
    dealTakeOver(this.taskMsg.taskId).then((res) => {
      if (res.error) {
        this.$message.error('接管失败：' + res.error.message);
      } else {
        this.$message.success('接管成功');
        this.getTrackingTaskList();
      }
    });
  }
  // 取回
  withdraw() {
    dealWithdraw(this.taskMsg.taskId).then((res) => {
      if (res.error) {
        this.$message.error('取回失败：' + res.error.message);
      } else {
        this.$message.success('取回成功');
        this.getTrackingTaskList();
      }
    });
  }
  // 流程催办
  processUrge() {
    const params = { processInstanceId: this.taskMsg.processInstanceId };
    dealProcessUrge(params).then((res) => {
      if (res.error) {
        this.$message.error('流程催办失败：' + res.error.message);
      } else {
        this.$message.success('流程催办成功');
        this.getTrackingTaskList();
      }
    });
  }
  // 节点催办
  nodeUrge() {
    dealNodeUrge(this.taskMsg.taskId).then((res) => {
      if (res.error) {
        this.$message.error('节点催办失败：' + res.error.message);
      } else {
        this.$message.success('节点催办成功');
        this.getTrackingTaskList();
      }
    });
  }
  // 查看流转历史
  flowHistory() {
    // 弹出任务办理状态流程流程图
    this.histroyDialog = true;
  }
  // 关闭流转历史弹出
  cancelHistroyDialog() {
    this.histroyDialog = false;
  }
  // 面板确定按钮
  saveDialog() {
    if (!this.dialogSelect) {
      this.$message.error(this.dialogSelectContent);
      return false;
    }
    switch (this.dialogTitle) {
      case '跳过任务节点':
        this.sureSkip();
        break;
      case '指派':
        this.sureAssign();
        break;
    }
  }
  // 面板取消按钮
  cancelDialog() {
    this.dialogVisible = false;
    this.dialogSelect = '';
    this.dialogSelectList = [];
  }
  // 跳过
  skip() {
    this.dialogTitle = '跳过任务节点';
    this.dialogSelectContent = '请选择下一待执行的节点';
    this.getCanSkipList();
    this.dialogVisible = true;
  }
  // 可跳过列表查询
  getCanSkipList() {
    const params = {
      processDefinitionId: this.taskMsg.processDefinitionId,
      taskDefinitionKey: this.taskMsg.taskDefinitionKey
    };
    queryCanSkipList(params).then((res: any) => {
      if (res.data) {
        this.dialogSelectList = res.data.map((item: any) => {
          const obj = {
            id: item.nodeId,
            name: item.nodeName
          };
          return obj;
        });
      } else {
        this.$message.error('可跳过节点列表查询失败：' + res.error.message);
      }
    });
  }
  // 跳过确定
  sureSkip() {
    const params = {
      activityId: this.dialogSelect,
      taskId: this.taskMsg.taskId
    };
    dealSkip(obj2FormData(params)).then((res: any) => {
      if (!res.error) {
        this.$message.success('任务跳过成功');
        this.dialogSelectContent = '';
        this.dialogSelectList = [];
        this.dialogSelect = '';
        this.dialogVisible = false;
        this.getTrackingTaskList();
      } else {
        this.$message.error('任务跳过失败：' + res.error.message);
      }
    });
  }
  // 指派
  assign() {
    this.dialogTitle = '指派';
    this.dialogSelectContent = '请选择指派人';
    this.dialogVisible = true;
  }
  // 指派确定
  sureAssign() {
    console.log('指派接口暂无');
    this.dialogVisible = false;
  }
  // 结束
  end() {
    this.$confirm('确定要结束该任务吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const params = { taskId: this.taskMsg.taskId };
        dealEnd(obj2FormData(params)).then((res) => {
          if (res.error) {
            this.$message.error('结束失败：' + res.error.message);
          } else {
            this.$message.success('结束成功');
            this.getTrackingTaskList();
            this.$emit('done');
          }
        });
      })
      .catch(() => {});
  }
}
