












































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import FlowChartPreview from './components/flowChartPreview.vue';
import { arrayFind } from '@utils/util';
import {
  queryActivatedList,
  dealManuallyStart,
  getManuallyList,
  queryProcessName
} from '@api/processAssignment';

@Component({
  components: { FlowChartPreview }
})
export default class NeedActivatedTask extends Vue {
  @Prop({ default: [] }) readonly processTagsList: any;
  private queryParams: any = {
    limit: 10,
    page: 1,
    processName: [],
    processTags: [],
    checked: false
  };
  private btnLoading = false;
  private state = false;
  // private processTagsList: Array<any> = [];
  private tableLoading = false;
  private tableTotal = 0;
  private tableDataList: Array<any> = [];
  private processNameList: Array<any> = [];
  private columnsList: Array<any> = [
    {
      type: 'index',
      label: '序号',
      align: 'center',
      width: 60
    },
    {
      prop: 'processName',
      label: '流程名称',
      'header-align': 'left',
      'show-overflow-tooltip': true
    },
    {
      label: '操作',
      align: 'center',
      actions: [
        {
          // code: 'initiated',
          text: '手工发起',
          onClick: this.manuallyStart
        },
        {
          text: '流程图预览',
          onClick: this.preview
        }
      ]
    }
  ];
  private dialogVisible = false;
  private viewPublish = false; // 显示已发布流程图与否
  private manuallyStartDialog = false;
  private manuallyStartSelected = '';
  private manuallyStartList: Array<any> = [];
  private startRow: any = {};

  private mounted() {
    this.getProcessName();
  }
  init() {
    this.getActivatedTaskList();
    this.getProcessName();
  }
  handleCheckoutChange() {
    this.getActivatedTaskList();
  }
  // 标签点击
  selectTag(tag: any) {
    if (tag === '全部') {
      if (this.queryParams.processTags.length === this.processTagsList.length) {
        this.queryParams.processTags = [];
      } else {
        this.queryParams.processTags = JSON.parse(
          JSON.stringify(this.processTagsList)
        );
      }
    } else {
      const tagIndex = this.queryParams.processTags.indexOf(tag);
      if (tagIndex === -1) {
        this.queryParams.processTags.push(tag);
        if (
          this.queryParams.processTags.length ===
          this.processTagsList.length - 1
        ) {
          this.queryParams.processTags.push('全部');
        }
      } else {
        this.queryParams.processTags.splice(tagIndex, 1);
        const allTag = this.queryParams.processTags.indexOf('全部');
        if (allTag !== -1) {
          this.queryParams.processTags.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }
  getProcessName() {
    if (this.processNameList.length === 0) {
      queryProcessName().then((res) => {
        if (res.data) {
          this.processNameList = res.data;
        } else {
          this.$message.error('标签列表查询失败：' + res.error.message);
        }
      });
    }
  }
  // 表格数据查询
  getActivatedTaskList() {
    const params = {
      onlyShowMe: this.queryParams.checked ? 1 : 0,
      limit: this.queryParams.limit,
      page: this.queryParams.page,
      processTags: JSON.parse(JSON.stringify(this.queryParams.processTags))
    };
    if (this.processNameList.length > 0) {
      params['templateIds'] = this.queryParams.processName.map((pid) => {
        return arrayFind(this.processNameList, 'publishId', pid).id;
      });
    }
    if (params.processTags.indexOf('全部') !== -1) {
      const index = params.processTags.indexOf('全部');
      params.processTags.splice(index, 1);
    }

    this.tableLoading = true;
    queryActivatedList(params).then((res) => {
      this.tableLoading = false;
      if (res.data) {
        this.tableTotal = res.data.total;
        this.tableDataList = res.data.rows;
      } else {
        this.$message.error('待发起任务列表查询失败：' + res.error.message);
      }
    });
  }
  // 搜索
  handleSearch() {
    this.queryParams.page = 1;
    this.getActivatedTaskList();
  }
  // 重置
  handleReset() {
    this.queryParams.processName = [];
    this.queryParams.processTags = [];
    this.queryParams.checked = false;
  }
  // 当前页变化
  handleCurrent(e: any) {
    this.queryParams.page = e;
    this.getActivatedTaskList();
  }
  // 页大小变化
  handleSize(e: any) {
    this.queryParams.limit = e;
    this.queryParams.page = 1;
    this.getActivatedTaskList();
  }
  // 手动发起
  manuallyStart(row: any) {
    this.startRow = row;
    this.manuallyStartDialog = true;
    this.manuallyStartSelected = '';
    this.manuallyStartList = [];
    this.getManuallyStartList();
  }
  // 手工发起名称列表查询
  getManuallyStartList() {
    getManuallyList(this.startRow.publishId).then((res) => {
      if (res.data) {
        this.manuallyStartList = res.data;
        if (this.manuallyStartList.length === 1) {
          this.manuallyStartSelected = this.manuallyStartList[0].handName;
        }
      } else {
        this.$message.error('手工发起列表失败：' + res.error.message);
      }
    });
  }
  // 手工发起弹框确定
  manuallyStartSave() {
    if (this.state) {
      return false;
    }
    this.state = true;
    if (this.manuallyStartSelected) {
      let info: any = '';
      for (const i in this.manuallyStartList) {
        if (this.manuallyStartList[i].handName === this.manuallyStartSelected) {
          info = this.manuallyStartList[i];
        }
      }
      const params = {
        processDefinitionKey: this.startRow.processDefinitionKey,
        dataViewId: info.dataViewId,
        publishId: info.publishId,
        ruleArray: info.ruleArray,
        launchingModeName: info.handName,
        handCode: info.handCode,
        processSplit: info.processSplit,
        timingLaunchIds: info.timingLaunchIds
      };
      this.btnLoading = true;
      dealManuallyStart(params).then((res) => {
        this.btnLoading = false;
        this.state = false;
        if (res.error) {
          this.$message.error('手工发起失败：' + res.error.message);
        } else {
          this.$message.success('手工发起成功');
          this.manuallyStartDialog = false;
          if (res.data.canJump === true) {
            this.$router.push({
              path: '/processCenter/taskDeal',
              query: {
                taskId: res.data.taskId,
                processInstanceId: res.data.processInstanceId,
                mode: 'deal'
              }
            });
          }
        }
      });
    } else {
      this.$message.error('请先选择发起任务名称');
    }
  }
  // 手工发起弹框取消
  manuallyStartCancel() {
    this.manuallyStartDialog = false;
  }
  // 弹框关闭
  cancelDialog() {
    this.dialogVisible = false;
  }
  // 流程图预览
  preview(row: any) {
    this.startRow = row;
    this.viewPublish = true;
    this.dialogVisible = true;
  }
}
